import Profile from "./Profile";
import React from "react";
import {getUserProfile, getUserStatus, savePhoto, updateUserStatus} from "../../redux/profileReducer";
import {connect} from "react-redux";
import {
    Navigate,
    useLocation,
    useNavigate,
    useParams,
} from "react-router-dom";
import {withAuthRedirect} from "../../hoc/WithAuthRedirect";
import {compose} from "redux"
export function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }
    return ComponentWithRouterProp;
}
class ProfileConteiner extends React.Component{
    refreshProfile(){
        let userId = this.props.router.params.userId;
        if (!userId){
            userId= this.props.authorizedUserId
        }
        this.props.getUserProfile(userId);
        this.props.getUserStatus(userId);
    }

    componentDidMount() {
        this.refreshProfile()
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.router.params.userId!= prevProps.router.params.userId) {
            this.refreshProfile()
        }
    }

    render(){
        if(!this.props.isAuth) return <Navigate to={'/login'}/>

        return <Profile {...this.props} profile={this.props.profile} status={this.props.status} updateUserStatus={this.props.updateUserStatus}
                        isOwner={!this.props.router.params.userId} savePhoto={this.props.savePhoto}/>
    }
}
let mapStateToProps=(state)=>({
    profile:state.profilePage.profile,
    status:state.profilePage.status,
    authorizedUserId:state.auth.userId,
    isAuth:state.auth.isAuth
})
export default compose (
    connect(mapStateToProps, {getUserProfile, getUserStatus,updateUserStatus,savePhoto}),
    withRouter,
    withAuthRedirect
)(ProfileConteiner)



