import Preloader from "../../common/preloader";
import ProfileStatus from "./ProfileStatus"
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = (props)=>{
    let userPhoto= 'https://sun9-63.userapi.com/impg/c858224/v858224757/1bf82f/dDn3reK2Ytw.jpg?size=320x320&quality=96&sign=68c613560d0983b7967bc3a9324f33f9&c_uniq_tag=QgwS6lTmDJPqFpBYmISaY3qipVqoV31nBKyhe9H5cy8&type=album'
    const onMainPhotoSelected=(e)=>{
        if (e.target.files.length){
            props.savePhoto(e.target.files[0])
        }
    }
    if (!props.profile){
        return <Preloader/>
    }
    return(
        <div>
            <div>
                <img src={props.profile.photos.large|| userPhoto}/>
                {props.isOwner&& <input type={"file"} onChange={onMainPhotoSelected}/>}
                <div><b>{props.profile.fullName}</b></div>
                <div>
                    <b>Looking for a job:</b> {props.profile.lookingForAJob?'Yes':'No'}
                </div>
                <ProfileStatusWithHooks status={props.status} updateUserStatus={props.updateUserStatus}/>
            </div>
        </div>
    )
}
export default ProfileInfo